@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
.App-header{height:75%;padding-top:15vh;display:flex;flex-direction:column;align-items:center;justify-content:center;color:white;font-family:'Varela', sans-serif}.avatar{height:325px;border:7px solid #397367;border-radius:50%}.name-headline{margin:2rem 0 0 0;font-size:72px;background-color:#CA4246;background-image:linear-gradient(45deg, #397367 16.666%, #63CCCA 16.666%, #5DA399 33.333%, #42858C 33.333%, #7F6A93 50%, #80b5ca 50%, #A0C193 66.666%, #62987f 66.666%, #476098 83.333%, #615475 83.333%);background-size:100%;background-repeat:repeat;-webkit-background-clip:text;-webkit-text-fill-color:transparent;animation:rainbow-text-simple-animation-rev 0.75s ease forwards}.name-headline:hover{animation:rainbow-text-simple-animation 0.5s ease-in forwards}@keyframes rainbow-text-simple-animation-rev{0%{background-size:650%}40%{background-size:650%}100%{background-size:100%}}@keyframes rainbow-text-simple-animation{0%{background-size:100%}80%{background-size:650%}100%{background-size:650%}}.quick-about{font-size:1.5rem}.placeholder{font-style:italic;font-size:.75rem;color:#7F6A93}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#282c34}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.App{min-height:100vh}

