@import url('https://fonts.googleapis.com/css2?family=Varela&display=swap');
@import './Variables';
.App-header {
  height: 75%;
  padding-top: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: 'Varela', sans-serif; }

.avatar {
  height: 325px;
  border: 7px solid $sea-green;
  border-radius: 50%; }

.name-headline {
  margin: 2rem 0 0 0;
  font-size: 72px;
  background-color: #CA4246;

  background-image: linear-gradient(45deg,$sea-green 16.666%,$medium-turquiose 16.666%,$pine 33.333%, $teal 33.333%, $french-lilac 50%, $light-blue 50%, $sage-green 66.666%, $dark-sage 66.666%, $ocean 83.333%, $medium-purple 83.333%);

  background-size: 100%;
  background-repeat: repeat;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  animation: rainbow-text-simple-animation-rev 0.75s ease forwards;
  &:hover {
    animation: rainbow-text-simple-animation 0.5s ease-in forwards; } }

@keyframes rainbow-text-simple-animation-rev {
  0% {
    background-size: 650%; }

  40% {
    background-size: 650%; }

  100% {
    background-size: 100%; } }


@keyframes rainbow-text-simple-animation {
  0% {
    background-size: 100%; }

  80% {
    background-size: 650%; }

  100% {
    background-size: 650%; } }



.quick-about {
  font-size: 1.5rem; }

.placeholder {
  font-style: italic;
  font-size: .75rem;
  color: $french-lilac; }

